<template>
  <div>
    <div class="top_der">
      <h3>待办任务清单</h3>

    <van-form @submit="onSubmit">
      <van-field v-model="task" name="task" placeholder="请输入任务名" />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
    </div>
    <div class="center_der">
      <van-list>
      <van-cell v-for="item in list" :key="item.id" class="list_der"> 
        <van-checkbox v-model="item.done" @change="changeSonCheak(item.id)">
          <p style="font-size: 16px">
            {{ item.thing }}
          </p>
        </van-checkbox>
      </van-cell>
    </van-list>
    </div>
    
    <div v-show="this.list.length>0" class="foot_der">
      <van-checkbox v-model="checkedAll" style="margin-left: 16px; margin-top: 10px" @change="selectAll">已选中 {{ sum }} 项任务 </van-checkbox>
    <van-button type="danger" size="small" style="float: right; margin-bottom: 80px; margin-top: -30px; margin-right: 20px" @click="deleteItem">删除选中内容</van-button>
    </div>
    
  </div>
</template>

<script>
import { nanoid } from 'nanoid'
import { Dialog } from 'vant'
export default {
  name: 'MyTask',
  data() {
    return {
      task: '',
      activeName: '0',
      flag: 1,
      checkedAll: false,
      list: JSON.parse(localStorage.getItem('task'))||[]
    }
  },
  methods: {
    onSubmit(values) {
      console.log(values)
      if (values.task.trim() == '') {
        this.task = ''

      return  Dialog({ message: '任务名不能为空！' })
      } else {
        this.list.some((item) => {
          if (values.task.trim() == item) {
            return Dialog({ message: '已有相同任务，不能重复添加！' })
          }
        })
      }
      this.list.unshift({
        id: nanoid(),
        thing: values.task,
        done: 0
      })
      localStorage.setItem('task',JSON.stringify(this.list))
      
      this.task = ''
    },
    setImportant() {
      this.flag = 0
    },
    setNotImportant() {
      this.flag = 1
    },
    selectAll() {
      if (this.checkedAll) {
        this.list.forEach((item) => {
          item.done = true
        })
      } else {
        let flag = this.list.every((item) => {
          if (item.done == true) {
            return true
          }
        })
        if (flag) {
          this.list.forEach((item) => {
            item.done = false
          })
        }
      }
    },
    changeSonCheak(id) {
      let flag = this.list.every((item) => {
        if (item.done == true) {
          return true
        }
      })
      if (flag) {
        this.checkedAll = true
      } else {
        this.checkedAll = false
      }
    },
    deleteItem() {
      let result = this.list.filter((item) => {
        return item.done == false
      })
      this.list = result
      this.checkedAll = false
    }
  },
  computed: {
    sum() {
      let sum = 0
      this.list.forEach((item) => {
        if (item.done == true) {
          sum++
        }
      })
      return sum
    },
    checkedAllLis() {
      let flag = this.list.every((item) => {
        return item.done
      })
      if (flag) {
        return (this.checkedAll = true)
      } else {
        return (this.checkedAll = false)
      }
    }
  }
}
</script>

